@import url(https://fonts.googleapis.com/css?family=Noto+Sans+JP|Noto+Serif+JP:900&display=swap&subset=japanese);
body {
  background-color: #313131;
  margin: 0;
  padding: 0;
}

.App {
  font-family: 'Noto Sans JP', sans-serif;
  max-width: 640px;
  margin: 10px auto;
  background-color: white;
  padding: 10px;
}

.Title {
  position: relative;
  padding: 15px;
  color: #313131;
  font-family: 'Noto Serif JP', serif;
  font-size: xx-large;
  text-align: center;
  background: repeating-linear-gradient(-45deg, #E8E8E8, #E8E8E8 10px, #FFF 0, #FFF 20px);
  border-bottom: #C65544 solid 3px;
  border-top: white solid 3px;
  overflow-wrap: break-word;
}

.Title:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -3px;
  width: 33%;
  height: inherit;
  border-bottom: 3px solid #ffffff;
}

/* いい感じの区切りで文を改行するように */
.Title span {
  display: inline-block;
}

.ButtonList .group {
  margin: 0.5rem auto;
}

.TagButton {
  cursor: default;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.TagButton.checked {
  background-color: #1598DD;
}

.tag {
  margin: 4px;
  display: inline-block;
  font-size: small;
  width: 100px;
  height: 1.5rem;
  line-height: 1.5rem;
  text-align: center;
  box-shadow: 0px 2px 1px 2px;
  color: #E8E8E8;
  background-color: #313131;
}

.ClearButton {
  font-weight: 'bold';
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.ClearButton:active {
  background-color: #1598DD;
}

.noinfo {
  text-align: center;
  color: #AAAAAA;
  border: solid 1.5px #AAAAAA;
  padding: 8px;
}

.Result {
  margin: 8px 0px;
  padding: 0px 8px;
  background-color: #E8E8E8;
}

.Result.rarity6 {
  background-color: goldenrod;
}

.Result.rarity5 {
  background-color: palegoldenrod;
}

.Result.rarity4 {
  background-color: lightblue;
}

.Result .note {
  color: #C65544
}

.Result .character {
  display: inline-block;
  margin: auto 6px
}

.Result .tag {
  box-shadow: none;
}

